/*src/main/frontend/components/NavBar.css*/

.navbar {
    width: 250px;
    background-color: #282c34;
    color: white;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 20px;
    position: fixed;
    top: 0;
    left: 0;
    transition: width 0.3s ease;
}

.navbar.closed {
    width: 65px;
}

.navbar-header {
    display: flex;
    justify-content: flex-end;
    width: 100%;
    transition: justify-content 0.3s;
    padding-bottom: 10px;
}

.navbar.closed .navbar-header {
    justify-content: center;
}

.hamburger-icon {
    font-size: 24px;
    cursor: pointer;
    transition: font-size 0.3s, left 0.3s;
    position: relative;
}

.navbar:not(.closed) .hamburger-icon {
    left: -10px;
}

.navbar.closed .hamburger-icon {
    left: 0;
}

.navbar-list {
    list-style-type: none;
    padding: 0;
    width: 100%;
    margin: 0;
}

.navbar-item {
    width: 100%;
    margin-bottom: 10px;
    display: flex;
    justify-content: flex-start;
    transition: justify-content 0.3s ease;
}

.navbar.closed .navbar-item {
    justify-content: center;
}

.navbar-link {
    display: flex;
    align-items: center;
    padding: 10px 20px;
    color: white;
    text-decoration: none;
    font-size: 20px;
    width: 100%;
    box-sizing: border-box;
}

.navbar-text {
    margin-left: 10px;
    white-space: nowrap;
    transition: opacity 0.3s ease-in-out;
    opacity: 0;
    visibility: hidden; /* 기본적으로 텍스트 숨김 */
}

.navbar.opened .navbar-text {
    opacity: 1;
    visibility: visible;
}

.navbar.closed .navbar-text {
    opacity: 0;
    visibility: hidden;
}

.navbar-link:hover {
    background-color: #9370db;
    box-shadow: 0 4px 8px rgba(106, 77, 171, 0.8);
}

.navbar-icon {
    font-size: 24px;
    transition: margin-right 0.3s ease;
    flex-shrink: 0; /* 아이콘이 항상 크기를 유지하도록 설정 */
}

.navbar.closed .navbar-icon {
    margin-right: 0;
}
