/* src/main/frontend/src/pages/SchedulePage.css */

.right-section {
    width: 40%; /* 기존에 설정된 너비 */
    background-color: #f7f7f7;
    padding: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    height: 500px; /* 고정된 높이 설정 */
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden; /* 콘텐츠가 영역을 넘어가지 않도록 */
}

.schedule-table-container {
    width: 100%; /* right_section의 너비에 맞추기 */
    height: 100%; /* right_section의 높이에 맞추기 */
    display: flex;
    justify-content: center;
    align-items: center;
}

.schedule-table {
    width: 100%; /* 시간표가 섹션의 너비를 가득 채우도록 */
    height: 100%; /* 시간표가 섹션의 높이를 가득 채우도록 */
    table-layout: fixed; /* 각 열의 너비를 균일하게 고정 */
    border-collapse: collapse;
    overflow: hidden;
}

.schedule-table th,
.schedule-table td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: center;
    font-size: 12px;
    white-space: nowrap; /* 텍스트 줄바꿈 방지 */
}

.schedule-table th {
    background-color: #f4f4f4;
}

.schedule-table tbody tr:nth-child(odd) {
    background-color: #f9f9f9;
}

.schedule-table tbody tr:hover {
    background-color: #f1f1f1;
}
